import React, { useState } from 'react';
import PropTypes from 'prop-types';
import CartItem from './CartItem';
import { Button } from 'react-bootstrap';






const Cart = ({ items, total, currency, removeFromCart }) => {
  const [toggle, setToggle] = useState(false)

 
    
    return (
        <div>

            <h3>Shopping Cart</h3>
          
<Button onClick={() => {setToggle(!toggle)}} variant="outline-primary">Cart</Button>
  
          {(toggle === true) &&   <div className="cart">
                <div className="panel panel-default">
                    <div className="panel-body">
                        {items.length > 0 && (
                            <div className="cart__body">
                                {items.map(item => (
                                    <CartItem key={item.id} {...item} onClick={() => removeFromCart(item.id)} />
                                ))}
                            </div>
                        )}
                        {items.length === 0 && (
                          
                           <div className="alert alert-info">Cart is empty</div>
                       
                        )}
                        <div className="cart__total">Total: {total} {currency}</div>
                    </div>
                </div>
            </div>
          }
        

      
  

            
          
        </div>
    );
}

Cart.propTypes = {
    items: PropTypes.array,
    total: PropTypes.number,
    currency: PropTypes.string,
    removeFromCart: PropTypes.func.isRequired
}

export default Cart;
